import React, {useState, useEffect} from 'react';
import Tierinfo from '../Tierinfo';
import * as api from '../../../api/api';

const Body = ({leagueEntriesInfo}) => {
    const [soloInfo, setSoloinfo] = useState("");
    const [tftInfo, setTftinfo] = useState("");
    const [flexInfo, setFlexinfo] = useState("");
    return(
        <div className="rank_wrapper">
            {
                leagueEntriesInfo && leagueEntriesInfo.filter((data, index) => {
                    if(data.queueType === "RANKED_SOLO_5x5") {
                        return setSoloinfo(data);
                    } else if(data.queueType === "RANKED_TFT") {
                        return setTftinfo(data);
                    } else if(data.queueType === "RANKED_FLEX_SR") {
                        return setFlexinfo(data);
                    }
                })
            }
            <Tierinfo name="솔로랭크" data={soloInfo}/>
            <Tierinfo name="자유랭크" data={tftInfo}/>
            <Tierinfo name="롤토체스" data={flexInfo}/>
            <style jsx>
                {`
                    .rank_wrapper {
                        padding-top: 40px;
                        width: 300px;
                    }
                `}
            </style>
        </div>
    );
};

export default Body;